import { PropsWithChildren } from "react"
import { Box, Typography } from "@rp/react/web/mui/core"

type TabProps = PropsWithChildren & {
  value?: number
  index?: number
  padding?: number
}

const TabPanel = (props: TabProps) => {
  const { children, value, index, padding = 3, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      aria-labelledby={`tab-panel-${index}`}
      style={{ flexGrow: 1 }}
      {...other}
    >
      {value === index && (
        <Box p={padding}>
          <Typography component="div">{children}</Typography>
        </Box>
      )}
    </div>
  )
}

export { TabPanel }
export type { TabProps }
